export default {
	data() {
		return {
			showLoding:false,
			showMore:false,
			moreTip:"加载中...",
			videoList:[{
				src:"http://vd4.bdstatic.com/mda-jjkfpke6n90q9t7m/sc/mda-jjkfpke6n90q9t7m.mp4",
				text:'关于视频的描述',
				videoImg:require("@/static/images/video_img1.png"),
				playNum:120,
				author:'作者',
				authorHeader:"",
				goods:100,
				showVideo:false,
				danmuList:[{
				    text: '第 1s 出现的弹幕',
				    color: '#ff0000',
				    time: 1
				},
				{
				    text: '第 3s 出现的弹幕',
				    color: '#ff00ff',
				    time: 3
				}]
			},{
				src:"http://vd2.bdstatic.com/mda-je6r0ggchviruncv/sc/mda-je6r0ggchviruncv.mp4",
				text:'关于视频的描述',
				videoImg:require("@/static/images/video_img2.png"),
				playNum:120,
				author:'作者',
				authorHeader:"",
				goods:100,
				showVideo:false,
				danmuList:[{
				    text: '第 1s 出现的弹幕',
				    color: '#ff0000',
				    time: 1
				},
				{
				    text: '第 3s 出现的弹幕',
				    color: '#ff00ff',
				    time: 3
				}]
			}]
			
		}
	},
	//页面隐藏时暂停
	onHide:function(){
		if(this.videoContext){
			this.videoContext.pause()
		}
	},
	// 监听下拉刷新过程
	onPullDownRefresh() {
		this.showLoding = true;
	    this.getRes()    
	},
	// 监听上拉加载过程
	onReachBottom(){
		 // console.log("到底了,要重新拉取数据了")
		 this.showMore = true;
		 setTimeout(()=> {
			this.moreTip = "没有更多了";
			setTimeout(()=>{
				this.showMore = false;
				this.moreTip = "加载中...";
			},500)
		 }, 1500);
	},
	methods: {
		// 去请求数据
		getRes(){
			setTimeout(()=> {
			    uni.stopPullDownRefresh()
				this.showLoding = false;
			}, 1000);
		},
		videoErrorCallback: function(e) {
			uni.showModal({
				content: e.target.errMsg || "该环境不支持播放此视频",
				showCancel: false
			})
		},
		whereVideo(index){
			let nowItem = this.videoList;
			for(let i=0;i<nowItem.length;i++){
				if(i==index){
					nowItem[i].showVideo = true;
				}else{
					nowItem[i].showVideo = false;
				}
			}
			this.videoList = nowItem;

			this.videoContext = uni.createVideoContext('myVideo'+index);
			this.videoContext.play();

		}
	}
}
